.header {
  background-color: #f7f7f9;
  max-width: 1440px;
  height: 86px;
  display: flex;
  align-items: center;
  padding: 0 50px;

  &_white {
    background-color: #fff;
  }

  &__wrapper {
    margin: 0 auto;
    display: flex;
    flex: 0 1 1170px;
    justify-content: space-between;
    align-items: center;
  }

  &__burger {
    box-sizing: content-box;
    padding: 10px;
    display: none;
    flex: 0 0 22px;
    height: 22px;
  }

  &__logo {
    width: 136px;
    height: 28px;
    cursor: pointer;
  }
}

.menuList,
.iconsList {
  list-style: none;
  padding: 0;
  display: flex;
}

.menuList {
  gap: 3.05vw;
  margin-right: 1.8vw;

  &__link {
    text-transform: uppercase;
    font-size: 13px;
    text-decoration: none;
    color: initial;
    opacity: 1;
    transition: 0.2s;

    &:hover {
      opacity: 0.7;
    }
  }
}

.iconsList {
  gap: 24px;

  &__item {
    display: flex;
    align-items: center;
  }

  &__icon {
    width: 22px;
    height: 22px;
    cursor: pointer;
    opacity: 1;
    transition: 0.2s;

    &:hover {
      opacity: 0.7;
    }
  }
}

@media screen and (max-width: 1000px) {
  .header {
    padding: 0 30px;
  }

  .menuList {
    gap: 2.5vw;
    margin-right: 0;
  }
}

@media screen and (max-width: 860px) {
  .header {
    &__logo {
      width: 114px;
      height: 24px;
    }
  }

  .menuList {
    gap: 1.5vw;
  }

  .iconsList {
    gap: 17px;
  }
}

@media screen and (max-width: 768px) {
  .header {
    padding: 0 40px 0 30px;
    height: 70px;

    &__wrapper {
      justify-content: space-between;
    }

    &__burger {
      display: block;
    }

    &__logo {
      position: relative;
      left: 31px;
    }
  }

  .menuList {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  .header {
    padding: 0 15px 0 5px;
    height: 60px;
  }

  .iconsList {
    gap: 14px;
  }
}
